import f from "@/application/functions";
import {relativeTimeThreshold} from "moment";
import InputField from './InputField/InputField.vue'
import _ from "underscore";


export default {
  name: 'chatInput',
  props: {
    chat: Object,
    u: String
  },


  components: {InputField},

  data: function () {

    return {
      upload: true,
      info: {},
      loading: false,
      text: '',
      file: {},
      fileInfo: {},
      ready : false,
      menuItems: [
        {
          click: "cameraHandler",
          title: "Take photo or Video",
          icon: "fas fa-camera",

          upload: {
            multiple: true,
            extensions: ['jpg', 'jpeg', 'png'],
            images: {
              resize: {
                type: 'fit'
              }
            }
          }
        },

        {
          click: "fileHandler",
          title: "Send File",
          icon: "fas fa-sticky-note",

          upload: {
            multiple: true
          },
        }
      ],
      anyUrlMeta: String,
    }

  },


  watch: {
    usersforkeys : {
      immediate : true,
      handler : function(){
        this.downloadKeys()
      }
    }
  },


  computed: {
    uusers: function(){
      if(this.u){
        return _.map(this.u.split(','), (u) => {
          return u
        })
      }

      return []
    },
    ausers : function(){
      if(this.u){
        return _.map(this.u.split(','), (u) => {
          return this.core.user.matrixId(u)
        })
      }

      return []
    },

    invited : function(){
      
      if(!this.chat) {

        if(this.u) return this.ausers

        return []

      }

      return _.map(_.filter(this.chat.currentState.members, function(m, v){
        return m.membership == 'invite'
      }), function(u){
        return u.userId
      })
    },

    joined : function(){
      
      if(!this.chat) return []

      return _.map(_.filter(this.chat.currentState.members, function(m, v){
        return m.membership == 'join'
      }), function(u){
        return u.userId
      })
    },

    usersforkeys : function(){
      return this.invited.concat(this.joined)
    }

  },

  created() {

  },

  mounted() {
    this.downloadKeys().then(r => {
      this.ready = true
    })
  },

  methods: {

    wait : function(){
      return this.$f.pretry(() => {
        return this.core.mtrx.client && this.core.mtrx.access && this.usersforkeys.length
      })
    },

    downloadKeys: function (){

      return this.wait().then(r => {
        return this.core.mtrx.client.downloadKeysForUsers(this.usersforkeys, {token : this.core.mtrx.access.accessToken})
      })
      
    },

    emitInputData: function () {
      this.$emit('emptyInput')
      this.upload = true
    },
    
    HideUploadPic() {
      this.upload = false
    },

    emitUrl: function (url) {
      this.$emit('setMetaUrl', url)
    },

    newchat: function () {
      if (this.u) {

        this.$store.state.globalpreloader = true
        var matrixId = null

        this.core.user.usersInfo(this.uusers).then(info => {

          var id = this.core.mtrx.kit.tetatetid(info[0], this.core.user.userinfo)

            matrixId = this.core.user.matrixId(info[0].id)

          var initialstate = [{
            "type": "m.room.custom",
            "state_key": "",
            "content": {
              "type" : "tetatet"
            },
          },
          {
            "type": "m.room.encryption",
            "state_key": "",
            "content": {
              "algorithm": "m.olm.v1.curve25519-aes-sha2"
            }
          }]

          return this.core.mtrx.client.createRoom(
            {
              room_alias_name: id,
              visibility: 'private',
              invite: [matrixId],
              name: '#' + id,
              initial_state: initialstate
              
              /*[
                {
                  "type": "m.room.guest_access",
                  "state_key": "",
                  "content": {
                    "guest_access": "can_join"
                  }
                },
                
              ]*/
            }
          )

        })
        
        .then(chat => {

          this.$store.state.globalpreloader = false

          /*this.$store.commit('icon', {
            icon : 'success',
            message : "Chat started"
          })*/

          console.log("CHAT", chat)

          this.$emit("newchat", chat)

          return this.core.mtrx.client.setPowerLevel(chat.room_id, matrixId, 100)

        }).catch(e => {

          console.log("E", e)

          this.$store.state.globalpreloader = false

          ////catch error

          /*this.$store.commit('icon', {
            icon : 'error',
            message : "Can't start chat"
          })*/
        })

      }

    },
   
    maySendMessage() {
      return this.chat && this.chat.maySendMessage()
    },

    greetings(){
      this.send('👋').then(r => {
        return Promise.resolve(r)
      })
    },

    sendinput(text, chatinput){
      this.send(text.textContent).then(r => {
        return Promise.resolve(r)
      })
    },

    send: function (text, chatinput) {

      console.log('text', text);
      console.log('chatinput', chatinput);

      let whichMsg = f.getUrl(text)

      var t = text

      if (!this.chat) {
        this.newchat()
      }

      this.$emit("sending")

      /*return this.core.mtrx.client.setRoomEncryption(this.chat.roomId, {
        algorithm : 'm.olm.v1.curve25519-aes-sha2'
      })*/

      return this.$f.pretry(() => {

        return this.chat

      }).then(() => {
        return this.core.mtrx.client.sendEvent(this.chat.roomId, "m.room.message", {

          body: t,
          msgtype: "m.text"

        }, "").then(r => {
          console.log(r)
        }).catch(e => {
          console.log(e)
        })

      })

      /*} else {
        // let url = this.anyUrlMeta
        let url = whichMsg
        let ts = 0
        let txt = text
        this.anyUrlMeta = ''

        this.$emit('inputClean', false)
        this.$emit("sending")

        return this.$f.pretry(() => {
          return this.chat

        }).then(() => {
          return this.core.mtrx.client.getUrlPreview(url, ts).then(response => {
   
            return response

          })
        }).then((result) => {

          result.msg = f.getTxt(txt)
          result.url = f.getUrl(txt)
          
          return JSON.stringify(result)

        }).then(r => {

          return this.core.mtrx.client.sendMessage(this.chat.roomId, {
            body: r,
            msgtype: 'm.metaLink'
          })

        }).catch(e => {
          console.log(e)

          //return Promise.reject(e)
        })
      }*/
    },

    sendImage: function (data) {
      
      this.$emit("sending")

      this.$f.pretry(() => {

        return this.chat

      }).then(() => {

        return this.core.mtrx.uploadContent(data.file)

      }).then((image) => {

        this.core.mtrx.client.sendImageMessage(this.chat.roomId, image, this.info, 'Image')
        this.$emit('previewImg', {})

      }).catch(e => {
        console.log(e)
      })
    },
    sendFile: function (data) {
      this.file = data.file
      
      this.$emit("sending")

      this.$f.pretry(() => {

        return this.chat

      }).then(() => {

        return this.core.mtrx.client.uploadContent(this.file)

      }).then(src => {
        return Promise.resolve(this.core.mtrx.client.mxcUrlToHttp(src))

      }).then((url) => {
        this.fileInfo.url = url
        this.fileInfo.name = this.file.name
        this.fileInfo.size = this.file.size
        let body = JSON.stringify(this.fileInfo)
        this.core.mtrx.client.sendMessage(this.chat.roomId, {
          body: body,
          msgtype: 'm.file'
        })
        this.$emit('anyFileSend', {})
        this.file = {}
      }).catch(e => {
        console.log(e)
      })
    },
    focus: function () {

    },

    blur: function () {
    },

    change: function () {
    },
    keyup: function (evt) {
      var value = evt.target.value

      if (value === '') {
        this.$emit('inputClean', false)
        return
      } else {
        this.$emit('inputClean', true)
      }

      this.text = value
      this.anyUrlMeta = f.getUrl(this.text)


      console.log('this.anyUrlMeta', this.anyUrlMeta)
      if (this.anyUrlMeta !== undefined) {
        this.$emit('setMetaUrl', this.anyUrlMeta)
      } else {
        this.$emit('inputClean', false)
      }

      if (this.chat)
        this.core.mtrx.client.sendTyping(this.chat.roomId, true, 100)
    },
    menuItemClickHandler(item, rowObject) {
      debugger
      this[item.click](rowObject);
      console.log(item, "item item")
    },
    menuItemLoadedHandler: function (value) {

      this.menuIsVisible = value

      return this.menuIsVisible
    },

    uploadStart(item, files) {
      console.log("uploadStartuploadStartuploadStartuploadStartuploadStartuploadStartuploadStart")
      
    },

    uploadError(item, error) {
      this.$store.commit('icon', {
        icon: 'error',
        message: error.text
      })
    },
    getImg() {
      return this.imgs = true
    },
    uploadSizeError(value) {
      if (!value) {
        this.$refs.dropdownMenu.hidePopup();
        console.log("error file size")
      }
    },
    uploadUploaded(item, data) {
      console.log("ASDASD")
      const validImageTypes = ['image/gif', 'image/jpeg', 'image/png'];

      if (!validImageTypes.includes(data.file.type)) {

        this.$emit('anyFileSend', data)
        this.sendFile(data)

      } else {

        this.$emit('previewImg', data)
        this.imageWH(data).then(info => {
          this.sendImage(data)
        })

      }
    },
    imageWH(file) {
      const img = new Image();
      var imgInfo = {};

      return new Promise((resolve, reject) => {

        img.onload = function () {
          imgInfo.w = this.width
          imgInfo.h = this.height
          resolve(imgInfo)
        }

        img.onerror = function () {
          reject()
        }

        img.src = file.base64
        this.info = imgInfo

      })


    },
    uploadUploadedAll(item, result) {
      this.$store.state.loading = false
      this.$refs.dropdownMenu.hidePopup();
    }

  },
}